.promo {
  padding: 119px 0 106px;
}

.promo__wrapper {
  display: flex;
  color: $color-default-white;

  @include vp-767 {
    flex-direction: column;
  }
}

.promo__info {

  width: 43.1%;
  padding: 83px 76px;
  background-color: $color-torch-red;

  @include vp-1199 {
    padding: 48px 6.8%;
    width: 43%;
  }

  @include vp-767 {
    width: 100%;
    padding: 49px 14.8%;
  }

  h2 {
    margin: 0;
    padding-top: 4px;
    padding-left: 15px;
    max-width: 200px;
    font-family: $ff-titles;
    font-weight: 600;
    font-size: 82px;
    line-height: 79px;
    text-align: left;
    letter-spacing: 0.03em;
    text-transform: uppercase;

    @include vp-1199 {
      width: 55%;
      max-height: 91px;
      padding-left: 7px;
      font-size: 48px;
      line-height: 46px;
    }

    @include vp-767 {
      padding-left: 9px;
    }
  }

  p {
    margin: 0;
    margin-bottom: 45px;
    font-size: 18px;
    line-height: 27px;

    @include vp-1199 {
      margin-bottom: 30px;
      width: auto;
      font-size: 12px;
      line-height: 18px;
    }

    @include vp-767 {
      margin-bottom: 31px;
    }
  }
}

.promo__text-wrapper {
  margin-bottom: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @include vp-1199 {
    margin-bottom: 0;
  }
}

.promo__list {
  margin: 0;
  width: 27%;


  @include vp-767 {
    width: 28%;
  }

  dt {
    font-size: 18px;
    line-height: 27px;

    @include vp-1199 {
      margin-bottom: -2px;
      font-size: 12px;
      line-height: 18px;
    }
  }

  dd {
    margin: 0;
    margin-bottom: 7px;
    font-weight: 700;
    font-size: 40px;
    line-height: 60px;
    position: relative;

    @include vp-1199 {
      margin-bottom: 1px;
      font-size: 24px;
      line-height: 36px;
    }

    p {
      margin: 0;
      font-weight: 400;
      font-size: 14px;
      line-height: 21px;

      @include vp-1199 {
        font-size: 10px;
        line-height: 15px;
      }
    }

    span {
      border-top: 1px solid $color-default-white;
    }
  }

  dd:last-child {
    margin-bottom: 0;
  }
}

