.promo__month {
  position: absolute;
  width: 70%;
  height: auto;
  top: 10px;
  left: 27px;

  @include vp-1199 {
    width: 76%;
    top: 3px;
    left: 16px;
  }
}

.promo__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 24px 100px;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $color-default-white;
  border: 4px solid #ffffff;

  @include hover {
    background-color: $color-default-white;
    box-shadow: 0 0 6px 4px $color-default-white;
    color: $color-torch-red;
  }

  @include focus {
    box-shadow: 0 0 6px 4px $color-default-white;
  }

  @include vp-1199 {
    min-width: 202px;
    padding: 13px 22px;
    font-size: 10px;
    line-height: 12px;
    border: 2px solid #ffffff;

    @include hover-focus {
      background-color: $color-transparent;
      box-shadow: none;
      color: $color-default-white;
      opacity: 0.8;

      &.focus-visible:focus {
        box-shadow: none;
      }
    }
  }

  @include vp-767 {
    padding: 11.5px 22px;
  }
}

.promo__link:focus {
  box-shadow: 0 0 6px 4px $color-default-white;

  @include vp-1199 {
    box-shadow: none;
  }
}

.promo__link:active {
  opacity: 0.8;
}

.promo__img {
  @include vp-1199 {
    width: 57%;
    height: 292px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  @include vp-767 {
    width: 100%;
  }
}
